import React from 'react';
import { Panel, PanelHeader, Group, Div, Button, ListItem, Avatar } from '@vkontakte/vkui';

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader>Rachel</PanelHeader>
        Тут тело
        {props.fetchedUser &&
<Group title="Результат из VK Connect">
	<ListItem
		before={props.fetchedUser.photo_200 ? <Avatar src={props.fetchedUser.photo_200}/> : null}
		description={props.fetchedUser.city && props.fetchedUser.city.title ? props.fetchedUser.city.title : ''}
	>
		{`${props.fetchedUser.first_name} ${props.fetchedUser.last_name}`}
	</ListItem>
</Group>}
        <Group title="Меню">
            <Div>
                <Button size="xl" level="secondary" onClick={props.go}>
                    Информация
                </Button>
            </Div>
        </Group>
    </Panel>
);
export default Home;